/* Interests.css */

.Interests {
  max-width: 1300px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin-bottom: 30px;
  opacity: 0; /* Start with 0 opacity */
  animation: fadeIn 1s ease-in-out forwards; /* Apply the fade-in animation */
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
  
  .Interests div {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
  }
  
  .Interests h4 {
    font-size: 20px;
  }
  
  .Interests img {
    width: 100%;
    height: 270px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 10px;
  }
  
  .Interests p {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
  
  /* Override justify-content for the last row */
  .Interests :last-child {
    justify-content: space-between;
    grid-gap: 50px;
  }
  
  /* Media query for screens smaller than 600px */
  @media screen and (max-width: 600px) {
    .Interests {
      grid-template-columns: 1fr; 
      margin-bottom: 50px;
    }
  }
  